body{
  background: rgb(114, 177, 174);
  }



#task-container{
  max-width:600px;
  margin:0 auto;
  
  background-color: #fff;
  
  margin-top:100px;
  margin-bottom:100px;

  justify-content: space-around;
  align-items: flex-start;

}

#form-wrapper{
  position: -webkit-sticky;
  position: sticky;
  top: 0rem;
  border-bottom: 1px solid  #e9e9e9;
  background-color: #fff;
  box-shadow: 0 3px 8px rgba(0,0,0,0.25);
  padding:40px;
}

#submit{
  background-color: #52c9c9;
  border-radius: 0;
  border:0;
  color: #fff;
}

.flex-wrapper{
  display: flex;
}

.task-wrapper{
    cursor: pointer;
    border-bottom: 1px solid  #e9e9e9;
    color: #686868;
    margin:5px;
    padding:20px;
}

.task-wrapper:hover{
    background-color: #F0F0F0;
}


